<template>
  <ul class="pagination" v-show="allBtnPagination">
    <li class="pagination__item"
    :class="currentBtnPagination == i ? 'pagination__item--current' : ''" 
    v-for="i in allBtnPagination"
    @click.stop="currentBtnPagination = i"
    :key="i"
    >{{i}}</li>
    <li>
      <select class="pagination__select" v-model="countFeedbacksOnPage" >
        <option value=3>3</option>
        <option value=5>5</option>
        <option value=7>7</option>
        <option value=9>9</option>
      </select>
    </li>
  </ul>
</template>

<script>
export default {
  data() {
    return {
      currentBtnPagination: 1,
      countFeedbacksOnPage: 5,
      arrFeedbacks: [],
    }
  },
  watch:{
    feedbacks(){
      const arr = [];
      Object.keys(this.feedbacks).forEach(key => arr.push(this.feedbacks[key]));  // переводим объект в массив
      this.arrFeedbacks = arr.reverse();
      this.setNewArr(); // установить отфильтрованный массив для отображения
    },
    currentBtnPagination(){
      this.setNewArr()
    },
    countFeedbacksOnPage(){
      this.currentBtnPagination = 1;
      this.setNewArr()
    }
  },
  computed:{
    allBtnPagination(){
      return Math.ceil(this.arrFeedbacks.length / this.countFeedbacksOnPage)
    },
  },
  methods:{
    setNewArr(){ // установить отфильтрованный массив для отображения в родителе
      const start = this.currentBtnPagination * this.countFeedbacksOnPage - this.countFeedbacksOnPage;
      const end = start + this.countFeedbacksOnPage;
      this.$parent.visibleFeedbacks = this.arrFeedbacks.slice(start, end);
    }
  },
  props: {
    feedbacks : Object
  }
}
</script>

