<template>
  <section class="form-add-feedback">
    <h2 class="form-add-feedback__title">Добавить отзыв</h2>
    <form class="form-add-feedback__form" @submit.prevent.stop="handlerSubmitBtn">
      <div class="form-add-feedback__wrapper">
        <div class="form-add-feedback__wrapper-name">
          <input class="form-add-feedback__input-name" id="name" type="text" v-model="inputName" minlength="5" maxlength="22">
          <label class="form-add-feedback__label-name" for="name"
          :class="inputName ? 'active' : ''" 
          >Имя</label>
        </div>
        <p class="form-add-feedback__wrapper-stars">
          <svg class="form-add-feedback__star" width="30px" height="30px" viewBox="-30 -20 570 570" stroke-width="40"
          v-for="i in 5" 
          :key="i"
          :class="i <= stars ? 'form-add-feedback__star--active' : ''" 
          @click.stop="stars=i">
		        <polygon points="255,402.212 412.59,497.25 370.897,318.011 510,197.472 326.63,181.738 255,12.75 183.371,181.738 0,197.472 139.103,318.011 97.41,497.25"/>>
          </svg>
        </p>
      </div>
      <div class="form-add-feedback__wrapper-text">
        <input class="form-add-feedback__input-text" id="text" type="text" v-model="inputText" minlength="3" maxlength="70">
        <label class="form-add-feedback__label-text" for="text"
        :class="inputText ? 'active' : ''"
        >Фидбэк</label>
      </div>
      <div class="form-add-feedback__wrapper-btn">
        <button class="form-add-feedback__btn-submit" 
        :disabled="inputName && inputText && stars ? false : true"
        type="submit">добавить </button>
      </div>
    </form>
  </section>
</template>

<script>
export default {
  data() {
    return {
      inputName: '',
      inputText: '',
      stars: 0,
    }
  },
  methods:{
    handlerSubmitBtn(){
      this.$parent.addNewItemInDB({
        name: this.inputName, 
        text: this.inputText, 
        stars: this.stars, 
        date: Date().toString().slice(4, 21)
      }); // добавить новую запись в датабэйс
      this.inputName = '';
      this.inputText = '';
      this.stars = 0;
    }
  },
}
</script>

